body {
  background-color: black;
}
/* defining grid layout */
.col-main {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin:  0 21% 0 21%;    
    justify-items: center;   
    grid-gap: 25px;
    padding-top: 10px;

}
  .col-1 img{    
    max-width: 100%;
    height: auto;
  }

  .col-1{
    width: 100%;
  }

  .col-1 p{
    color:#ffb800;
    font-weight: 500;
    font-style: italic;
    line-height: 1.6;
    font-size: 1rem;
    font-family: Montserrat,sans-serif;
  }

  .landing {
    text-align: left;
    margin:  0 21% 0 21%;  
  }

  .landing img {
    width: 316px;
    height: auto; 
  }

  .nav {
    background-color: #ffb800;
    height: 50px;
  }

  .nav-items {
    margin:  0 21% 0 21%;  
  }

  @media only screen and (max-width: 1200px) {
    .nav-items {
      margin: 0 5% 0 5%;
    }
  }


@media only screen and (max-width: 1200px) {
    .col-main {
      grid-template-columns: 1fr 1fr;
      margin: 0 5% 0 5%;
    }
  }

  @media only screen and (max-width: 1200px) {
    .landing {
      margin: 0 5% 0 5%;
    }
  }

  @media only screen and (max-width: 1200px) {
    .landing img {
      width: 250px;
      height: 25vh;
    }
  }

